import React from 'react';
import { Link } from 'react-router-dom';
import './css/about.css';

// import Button from 'antd/lib/button';
import {
    Button,
    Input,
    Layout
} from 'antd';

const InputGroup = Input.Group;
const { Header, Footer, Sider, Content } = Layout;

class AboutPanel extends React.Component {

    render() {
        return (
            <div className='about-panel'>
                <h3 className='about-title'>{this.props.title}</h3>
                {this.props.subTitle}
                <p>{this.props.desc}</p>

                {this.props.children}
            </div>
        );
    }

}

class AboutPanelSpecial extends React.Component{

    render() {
        return (
            <AboutPanel title="最后想说"
                        subTitle={<h4>时间飞逝,爱你身边每个爱你的人</h4>}
                        desc="在家好好听爸爸妈妈的话,顺便照顾好猫" />
        );
    }

}

class About extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            input: ''
        }
    }


    handleInput = (e)=>{
        console.log(e.target.value);
        this.setState({
            input: e.target.value
        })
    };

    render() {
        return (
            <div className='about'>


                <AboutPanel title="对你的印象"
                            desc="你是来自山东的小女生，看到的你的内在就像你的外表一样美丽,性格善良,朴实无华."/>

                <AboutPanel title="补充一点"
                            desc="女生要保护好自己,尤其是在网上个人资料不要随意泄露,特别是地址,联系方式相关等,我有个做主播的朋友被绑架过,具体...有空告诉你" >

                    <InputGroup>
                        <Input type="text" style={{width: '70%'}} value={this.state.input} onChange={this.handleInput}/>
                        <Button type='primary' onClick={()=> console.log(this.state.input)}>留言板</Button>
                    </InputGroup>

                </AboutPanel>

                <AboutPanelSpecial />

            </div>
        );
    }
}

export default About;



